import React from "react";
import { connect } from "react-redux";

import { setColor } from "../../../../../redux/monogram/monogram.actions";

const Color = ({ title, id, color, monogramColor, setColor }) => (
  <div
    className="_17Mu5"
    onClick={() => {
      if (id !== monogramColor.id) setColor({
        value: color,
        title, id
      });
    }}
  >
    <div className="_1kbpg">
      <span className="_3B6UA" style={{ color }} />
      <button className="c5qpw" />
    </div>
    <p className="MY7K5">{title}</p>
  </div>
);

const mapStateToProps = ({ monogram: { color } }) => ({
  monogramColor: color
});

const mapDispatchToProps = (dispatch) => ({
  setColor: (color) => dispatch(setColor(color))
});

export default connect(mapStateToProps, mapDispatchToProps)(Color);