import React from "react";
import { connect } from "react-redux";
import Gift from "./components/gift.component";
import CartItem from "./components/cart-item.component";
import FooterSection from "./components/footer-section.component";
import PageTitle from "../../components/page-title/page-title.component";
import { selectItems } from "../../redux/cart/cart.selectors";

const CartPage = ({ items }) => {
  return (
    <main className="_3FFLA _1AQ9-">
      <PageTitle title="My Cart" />
      <section className="_2ezKP _3AP5M">
        <div>
          {items.map((item, idx) => (
            <CartItem key={idx} {...item} />
          ))}
        </div>
        <Gift imageUrl="/assets/images/Giftbox1.png" price="$12.00" />
        <FooterSection />
      </section>
    </main>
  );
};

const mapStateToProps = (state) => ({
  items: selectItems(state)
});

export default connect(mapStateToProps)(CartPage);