import {
  typeSetStyle,
  typeSetInitials,
  typeSetColor,
  typeSetPlacement,
  typeSetPath,
  typeSetLetter,
} from "./action.types";

export const setStyle = (value) => ({
  type: typeSetStyle,
  payload: value,
});
export const setInitials = (value) => ({
  type: typeSetInitials,
  payload: value,
});
export const setColor = (value) => ({
  type: typeSetColor,
  payload: value,
});
export const setPlacement = (value) => ({
  type: typeSetPlacement,
  payload: value,
});
export const setPath = (value) => ({
  type: typeSetPath,
  payload: value,
});
export const setLetter = (value) => ({
  type: typeSetLetter,
  payload: value
})