import React from "react";
import { connect } from "react-redux";
import Navigator from "../navigator/navigator.component";
import Search from "../search/search.component";

const Sidenav = ({ hidden, section }) => (
  <section
    className={`_1AmTj${!hidden ? " _1km0o" : ""}`}
    style={{ color: "black" }}
  >
    <div className="_3gCK4" style={{ color: "inherit" }}>
      <div className="_233xR" />
      <div className="I7Tum">
        <div className="I7Tum menu-entered">
          {!hidden ? (
            section === "navigator" ? (
              <Navigator />
            ) : (
              <Search />
            )
          ) : null}
        </div>
      </div>
    </div>
  </section>
);

const mapStateToProps = ({ sidenav }) => ({
  hidden: sidenav.hidden,
  section: sidenav.section,
});

export default connect(mapStateToProps)(Sidenav);
