import React from "react";

const BigCard = ({ link, imageUrl, title, description, wide }) => (
  <a className={`_1d572${wide ? ' TB8KP' : ''}`} href={link}>
    <div
      className="_12cr2 _3xyQA _1ZoHX wBHJF"
      style={{
        backgroundImage:
          `url("${imageUrl}")`,
      }}
    >
      <div className="iUm2H" />
      <img
        className="_3YlSt"
        src={imageUrl}
        draggable="true"
        alt={title}
      />
    </div>
    <div className="SM-wF">
      <h5 className="_9YMHz">{title}</h5>
      <p className="_2xBBV">{description}</p>
    </div>
  </a>
);

export default BigCard;