import { createSelector } from "reselect";

const selectCart = (state) => state.cart;

export const selectItems = createSelector([selectCart], (cart) => cart.items);

export const selectTotalItems = createSelector([selectItems], (items) =>
  items.reduce(
    (accumalatedQuantity, item) => accumalatedQuantity + item.quantity,
    0
  )
);

export const selectTotalPrice = createSelector([selectItems], (items) =>
  items.reduce(
    (accumalatedPrice, item) => accumalatedPrice + item.quantity * item.price,
    0
  )
);