import React from "react";
import { connect } from "react-redux";

import {
  setDesignUpload,
  setDesignSrc,
} from "../../../../redux/design/design.actions";

import ModalSectionContainer from "../../../modal-section-container/modal-section-container.component";
import BackButton from "../../components/back-button.component";

const UploadSection = ({ setDesignUpload, setDesignSrc }) => {
  const openFileDialoge = () => {
    document.querySelector("#upload-design-input").click();
  };

  const uploadChanged = (event) => {
    const { target } = event;
    const reader = new FileReader();

    const file = target.files[0];

    if (!file) {
      setDesignUpload(null);
      setDesignSrc(null);
      return;
    }

    reader.onload = ({ target: { result } }) => setDesignSrc(result);
    reader.readAsDataURL(file);
    setDesignUpload(file);
  };

  return (
    <ModalSectionContainer title="Upload Design">
      <div
        className="_2g5gP _1C4yC"
        onClick={openFileDialoge}
        style={{
          backgroundImage: "url(/assets/images/upload.png)",
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center",
        }}
      >
        <input
          type="file"
          id="upload-design-input"
          name="upload-design"
          onChange={uploadChanged}
          style={{
            display: "none",
          }}
        />
      </div>
      <div className="_3KMGN">
        <div className="_1nnpm">
          <BackButton />
          <button className="_1FEMw _3ZdHm _3WIwG bp2Y3 _2ZSVP _3I2Tv">
            <span className="CPL8O">
              <span className="_2DBxJ">Upload</span>
            </span>
          </button>
        </div>
      </div>
    </ModalSectionContainer>
  );
};

const mapDispatchToProps = (dispatch) => ({
  setDesignUpload: (file) => dispatch(setDesignUpload(file)),
  setDesignSrc: (src) => dispatch(setDesignSrc(src)),
});

export default connect(null, mapDispatchToProps)(UploadSection);
