import React from "react";
import { connect } from "react-redux";
import { setDesignSection } from "../../../../redux/design-modal/design-modal.actions";

import ModalOptions from "../../../modal-options/modal-options.component";
import Option from "../../../modal-options/option.component";

const OptionsSection = ({ setDesignSection }) => (
  <ModalOptions>
    <Option
      title="Upload Design"
      sectionName="upload"
      setSection={setDesignSection}
    />
    <Option
      title="Placement"
      sectionName="placement"
      setSection={setDesignSection}
    />
    <Option
      title="Custom Align"
      sectionName="custom-align"
      setSection={setDesignSection}
    />
  </ModalOptions>
);

const mapDispatchToProps = (dispatch) => ({
  setDesignSection: (title) => dispatch(setDesignSection(title)),
});

export default connect(null, mapDispatchToProps)(OptionsSection);