const monogramReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case typeSetStyle:
      return {
        ...state,
        style: action.payload,
      };
    case typeSetInitials:
      return {
        ...state,
        initials: action.payload,
      };
    case typeSetColor:
      return {
        ...state,
        color: action.payload,
      };
    case typeSetPlacement:
      return {
        ...state,
        placement: action.payload,
      };
    case typeSetPath:
      return {
        ...state,
        paths: getChangedPaths(state.paths, action.payload),
      };
    default:
      return state;
  }
};

export default monogramReducer;
