import React from "react";
import { connect } from "react-redux";

import OptionsSection from "../sections/options-section/options.section";
import ColorSection from "../sections/color-section/color.section";
import InitialsSection from "../sections/initials-section/initials.section";
import PlacementSection from "../sections/placement-section/placement.section";
import StylesSection from "../sections/styles-section/styles.section";

const Section = ({ section }) => {
  switch (section) {
    case "options":
      return <OptionsSection />;
    case "styles":
      return <StylesSection />;
    case "initials":
      return <InitialsSection />;
    case "color":
      return <ColorSection />;
    case "placement":
      return <PlacementSection />;
    default:
      return <OptionsSection />;
  }
};

const mapStateToProps = ({ monogramModal: { section } }) => ({
  section,
});

export default connect(mapStateToProps)(Section);
