import React from "react";
import { connect } from "react-redux";
import { setMonogramSection } from "../../../../redux/monogram-modal/monogram-modal.actions";
import {
  selectColor,
  selectInitialsLetters,
  selectLimit,
  selectPaths,
} from "../../../../redux/monogram/monogram.selectors";

import ModalOptions from "../../../modal-options/modal-options.component";
import Option from "../../../modal-options/option.component";

const OptionsSection = ({ setMonogramSection, color, paths, limit }) => (
  <ModalOptions>
    <Option
      title="Monogram Styles"
      sectionName="styles"
      setSection={setMonogramSection}
    >
      <div className="_2nBBL">
        <div
          className="_12cr2 _34Wsx _31IMi wBHJF"
          style={{
            backgroundImage:
              'url("https://d3rv2fuz9136t1.cloudfront.net/monograms/images/000/000/015/original/V1000_2x.png?1567085992")',
          }}
        >
          <div className="iUm2H" />
          <img
            className="_3YlSt"
            src="https://d3rv2fuz9136t1.cloudfront.net/monograms/images/000/000/015/original/V1000_2x.png?1567085992"
            draggable="true"
            alt="Monogram Styles"
          />
        </div>
        <p className="_1A17w">V1000</p>
      </div>
    </Option>
    <Option
      title="Initials"
      sectionName="initials"
      setSection={setMonogramSection}
    >
      <p className="_44FS-">
        {limit % 2 === 0
          ? `${paths.left.letter}-${
              limit === 4
                ? `${paths.leftCenter.letter}-${paths.rightCenter.letter}`
                : ""
            }${paths.right.letter}`
          : `${limit > 1 ? `${paths.left.letter}-` : ""}${paths.center.letter}${
              limit > 1 ? `-${paths.right.letter}` : ""
            }`}
      </p>
    </Option>
    <Option title="Color" sectionName="color" setSection={setMonogramSection}>
      <div className="_3_iLi">
        <div>
          <span className="_3BmGS" style={{ color: color.value }} />
        </div>
        <p className="_1A17w">{color.title}</p>
      </div>
    </Option>
    <Option
      title="Placement"
      sectionName="placement"
      setSection={setMonogramSection}
    >
      <div className="_1WN-z">
        <div className="_12cr2 _1IXNd _31IMi">
          <span />
        </div>
      </div>
    </Option>
  </ModalOptions>
);

const mapStateToProps = (state) => ({
  color: selectColor(state),
  letters: selectInitialsLetters(state),
  limit: selectLimit(state),
  paths: selectPaths(state),
});

const mapDispatchToProps = (dispatch) => ({
  setMonogramSection: (title) => dispatch(setMonogramSection(title)),
});

export default connect(mapStateToProps, mapDispatchToProps)(OptionsSection);
