import React from "react";
import { Link } from "react-router-dom";

const SmallComp = ({ link, imageUrl }) => (
  <div className="vi5pO">
  <Link
    to={link}
    rel="noopener noreferrer"
    target="_blank"
    className="_1XkHG"
  >
    <div
      className="_12cr2 _2LN0h _31IMi wBHJF"
      style={{
        backgroundImage:
          `url("${imageUrl}")`
      }}
    >
      <div className="iUm2H" />
      <img
        className="_3YlSt"
        src={imageUrl}
        draggable="true"
        alt={link}
      />
    </div>
  </Link>
</div>
);

export default SmallComp;