import React from "react";
import { connect } from "react-redux";

import { setMonogramSection } from "../../../redux/monogram-modal/monogram-modal.actions";

const BackButton = ({ setMonogramSection }) => (
  <button
    type="button"
    className="_3se6M"
    onClick={() => setMonogramSection("options")}
  >
    back
  </button>
);

const mapDispatchToProps = (dispatch) => ({
  setMonogramSection: (section) => dispatch(setMonogramSection(section))
});

export default connect(null, mapDispatchToProps)(BackButton);