import React from "react";
import LinkTo from '../../components/link-to/link-to.component';

const ImageDiv = ({ link, imageUrl, classes, linkClass }) => (
  <LinkTo link={link} classes={linkClass}>
    <div
      className={classes}
      style={{
        backgroundImage: `url("${imageUrl}")`,
      }}
    >
      <div className="iUm2H" />
      <img className="_3YlSt" src={imageUrl} draggable="true" alt={link} />
    </div>
  </LinkTo>
);

export default ImageDiv;