import React from "react";
import { connect } from "react-redux";

import OptionsSection from "../sections/options-section/options.section";
import UploadSection from "../sections/upload-section/upload.section";
import PlacementSection from "../sections/placement-section/placement.section";
import CustomAlignSection from "../sections/custom-align-section/custom-align.section";
import { selectSection } from "../../../redux/design-modal/design-modal.selectors";

const Section = ({ section }) => {
  switch (section) {
    case "options":
      return <OptionsSection />
    case "upload":
      return <UploadSection />
    case "placement":
      return <PlacementSection />;
    case "custom-align":
      return <CustomAlignSection />;
    default:
      return <OptionsSection />;
  }
};

const mapStateToProps = (state) => ({
  section: selectSection(state)
});

export default connect(mapStateToProps)(Section);