import { typeSetDesignUpload, typeSetDesignPlacement, typeSetDesignSrc } from "./action.types";

export const setDesignUpload = (value) => ({
  type: typeSetDesignUpload,
  payload: value,
});
export const setDesignPlacement = (value) => ({
  type: typeSetDesignPlacement,
  payload: value,
});

export const setDesignSrc = (value) => ({
  type: typeSetDesignSrc,
  payload: value
})