import React from "react";
import { connect } from "react-redux";
// import { monogramColors } from "../../../local-db/local-db";

import SvgDiv from "./svg-div.component";

const SvgContainer = ({ paths, color, limit }) => (
  <div>
    {limit > 1 ? (
      <div>
        <SvgDiv color={color} path={paths.left.path} />
        {limit > 3 ? (
          <div>
            <SvgDiv color={color} path={paths.leftCenter.path} />
            <SvgDiv color={color} path={paths.center.path} />
            <SvgDiv color={color} path={paths.rightCenter.path} />
          </div>
        ) : limit === 3 ? (
          <SvgDiv color={color} path={paths.center.path} />
        ) : null}
        <SvgDiv color={color} path={paths.right.path} />
      </div>
    ) : (
      <SvgDiv color={color} path={paths.center.path} />
    )}
  </div>
);

const mapStateToProps = ({
  monogram: {
    paths,
    title,
    limit,
    color: { value },
  },
}) => ({
  title,
  paths,
  limit,
  color: value,
});

export default connect(mapStateToProps)(SvgContainer);
