import React from "react";
import PageBanner from "../../components/page-banner/page-banner.component";
import SectionOne from "./section-one";
import SectionThree from "./section-three";
import SectionTwo from "./section-two";

const MonogramPage = () => (
  <main className="_2Uaii">
    <PageBanner
      title="Monogram"
      description={`For generations, a personal monogram has represented family, history,
        and love. Matouk keeps this beautiful tradition alive and fresh,
        incorporating state-of-the-art production techniques to create modern
        and classic monogram styles. Virtually every product on Matouk.com may
        be monogrammed by choosing the Add a Monogram option on the product
        page.`}
    />
    <SectionOne />
    <SectionTwo />
    <SectionThree />
  </main>
);

export default MonogramPage;