import React from "react";
import { connect } from "react-redux";

import { showSidenav } from "../../redux/sidenav/sidenav.actions";
import BackButton from "./back-button.component";

const ShowSidenavButton = ({ showSidenav, back }) => (
  <div className="_1AE2U _1HouL" onClick={() => showSidenav("navigator")}>
    {back ? (
      <BackButton />
    ) : (
      <button type="button" className="_3gYxC">
        <i />
        <i />
        <i />
      </button>
    )}
  </div>
);

const mapDispatchToProps = (dispatch) => ({
  showSidenav: (section) => dispatch(showSidenav(section)),
});

export default connect(null, mapDispatchToProps)(ShowSidenavButton);
