import React from "react";

const InputField = ({ id, name, placeholder, required, classes, type = "input" }) => (
  <div className={`_3cjLo ${classes}`} id={id}>
    <label>
      <div className="_1yJAO">
        {
            type === 'input' ? <input required={required} type="text" name={name} placeholder={placeholder} /> 
            : <textarea required={required} type="text" name={name} placeholder={placeholder} />
        }
        <svg
          width={21}
          height={16}
          viewBox="0 0 21 16"
          xmlns="http://www.w3.org/2000/svg"
          className="_2Nu2h"
        >
          <path
            d="M20 1L6.626 14 1 8.532"
            stroke="black"
            strokeWidth={2}
            fill="none"
            fillRule="evenodd"
          />
        </svg>
      </div>
    </label>
  </div>
);

export default InputField;