import { typeHideModal, typeShowModal } from "./action.types";

const INITIAL_STATE = {
    hidden: true,
    section: "monogram"
  };
  
  const modalReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
      case typeShowModal:
        return {
          ...state,
          hidden: false,
          section: action.payload
        };
      case typeHideModal:
          return {
              ...state,
              hidden: true
          }
      default:
        return state;
    }
  };
  
  export default modalReducer;