import {
  typeSetDesignUpload,
  typeSetDesignPlacement,
  typeSetDesignSrc,
} from "./action.types";

const INITIAL_STATE = {
  upload: null,
  src: null,
  placement: 1,
};

const designReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case typeSetDesignUpload:
      return {
        ...state,
        upload: action.payload,
      };
    case typeSetDesignPlacement:
      return {
        ...state,
        placement: action.payload,
      };
    case typeSetDesignSrc:
      return {
        ...state,
        src: action.payload,
      };
    default:
      return state;
  }
};

export default designReducer;
