import {
  typeAddToCart,
  typeEmptyCart,
  typeRemoveFromCart,
  typeUpdateQuantity,
} from "./action.types";

export const addToCart = (item) => ({
  type: typeAddToCart,
  payload: item,
});

export const removeFromCart = (item) => ({
  type: typeRemoveFromCart,
  payload: item,
});

export const emptyCart = () => ({
  type: typeEmptyCart,
});

export const updateQuantity = (quantity) => ({
  type: typeUpdateQuantity,
  payload: quantity,
});