import React from "react";

const SvgDiv = ({ color, path }) => (
  <div className="nUTgN Zfe6O">
    <div>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
        width="348px"
        height="345px"
        viewBox="0 0 348 345"
        version="1.1"
        className="injected-svg"
      >
        <title>Artboard</title>
        <desc>Created with Sketch.</desc>
        <g
          id="Artboard"
          stroke="none"
          strokeWidth={1}
          fill="none"
          fillRule="evenodd"
        >
          <path d={path} fill="#FFFFFF" style={{ fill: color }} />
        </g>
      </svg>
    </div>
  </div>
);

export default SvgDiv;