import React from "react";
import { connect } from "react-redux";
import ModalOverview from "../../modal-overview/modal-overview.component";

const DesignOverview = ({ src }) => (
  <ModalOverview>
    <img
      width="380px"
      src={src || "/assets/images/triangle_PNG30.png"}
      alt="Sample Custom Design"
    />
  </ModalOverview>
);

const mapStateToProps = ({ design: { src } }) => ({
  src
});

export default connect(mapStateToProps)(DesignOverview);