import React from "react";
import SearchIcon from "../search-icon/search-icon.component";
import DesktopSearchForm from "./desktop-search-form.component";
import MobileSearchForm from "./mobile-search-form.component";
import HideSidenavButton from '../hide-sidenav-button/hide-sidenav-button.component';

const SearchForm = () => (
  <form id="searchInput" className="lMsyv _2cJPm">
    <div className="_17c4H">
      {document.body.offsetWidth < 1024 ? <HideSidenavButton /> : null}
      <input
        type="search"
        autoComplete="off"
        placeholder="Search Matouk"
        className="OuPeI"
      />
      <button
        className={`_12W9d${document.body.offsetWidth > 1023 ? " _2itao" : ""}`}
      >
        <SearchIcon />
      </button>
      <button type="reset" className="_3iG0t">
        <svg
          width={7}
          height={5}
          viewBox="0 0 7 5"
          xmlns="http://www.w3.org/2000/svg"
        >
          <g
            stroke="#FFF"
            fill="none"
            fillRule="evenodd"
            strokeLinecap="square"
          >
            <path d="M1.2.2l4.6 4.6" />
            <path d="M5.8.2L1.2 4.8" />
          </g>
        </svg>
      </button>
    </div>
    {document.body.offsetWidth > 1023 ? (
      <DesktopSearchForm />
    ) : (
      <MobileSearchForm />
    )}
  </form>
);

export default SearchForm;
