import React from "react";
import { Link } from "react-router-dom";

const Icon = ({ link, name }) => (
  <Link
    to={link}
    className="ee7t-"
    rel="noopener noreferrer"
    target="_blank"
  >
    <i className={`fa fa-${name}`} aria-hidden="true" />
  </Link>
);

export default Icon;