import React from "react";

import DetailsSection from "./sections/details.section";
import MainSection from "./sections/main.section";
import ImageSliderSection from "./sections/image-slider.section";
import ShareOptionsSection from "./sections/share-options.section";
import DescriptionSection from "./sections/description.section";

const ProductPage = ({ id, title, varient, price, imageUrl, colorName, link }) => (
  <main className="_3Uilw _2YhKB">
    <section id="style_feature" className="_20r9j">
      <h3 className="_NSG5">{title}</h3>
      <h1 className="_2_47f">{varient}</h1>
    </section>
    <div className="_2wJ4g">
      <ImageSliderSection />
      <DescriptionSection />
      <ShareOptionsSection />
      <MainSection id={id} title={title} varient={varient} price={price} imageUrl={imageUrl} colorName={colorName} link={link} />
      <DetailsSection title={title} />
    </div>
  </main>
);

export default ProductPage;
