import React from "react";
import { connect } from "react-redux";

import {setDesignSection} from "../../../redux/design-modal/design-modal.actions";

const BackButton = ({ setDesignSection }) => (
  <button
    type="button"
    className="_3se6M"
    onClick={() => setDesignSection("options")}
  >
    back
  </button>
);

const mapDispatchToProps = (dispatch) => ({
  setDesignSection: (section) => dispatch(setDesignSection(section))
});

export default connect(null, mapDispatchToProps)(BackButton);