const getTotalItems = (items) =>
  items.reduce(
    (accumalatedQuantity, item) => accumalatedQuantity + item.quantity,
    0
  );

const getTotalPrice = (items) =>
  items.reduce(
    (accumalatedPrice, item) => accumalatedPrice + item.quantity * item.price,
    0
  );

export const addToCart = (state, itemToAdd) => {
  const { items } = state;

  const newItems = items.find((item) => item.id === itemToAdd.id)
    ? items.map((item) => {
        return item.id === itemToAdd.id
          ? { ...item, quantity: item.quantity + 1 }
          : item;
      })
    : [...items, { ...itemToAdd, quantity: 1 }];

  return {
    ...state,
    items: newItems,
    totalItems: getTotalItems(newItems),
    totalPrice: getTotalPrice(newItems),
  };
};

export const removeFromCart = (state, itemToRemove) => {
  const { items } = state;

  const newItems = items.filter((item) => item.id !== itemToRemove.id);

  return {
    ...state,
    items: newItems,
    totalItems: getTotalItems(newItems),
    totalPrice: getTotalPrice(newItems),
  };
};

export const updateItemQuantity = (state, itemToUpdate) => {
  const { items } = state;

  const newItems = items.map((item) =>
    item.id === itemToUpdate.id
      ? { ...item, quantity: itemToUpdate.quantity }
      : item
  );

  return {
    ...state,
    items: newItems,
    totalItems: newItems.reduce(
      (accumalatedQuantity, item) => accumalatedQuantity + item.quantity,
      0
    ),
    totalPrice: getTotalPrice(newItems),
  };
};
