import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import RemoveFromCartButton from "../../../components/remove-from-cart-button/remove-from-cart-button.component";
import { updateQuantity } from "../../../redux/cart/cart.actions";

const CartItem = ({
  id,
  link,
  imageUrl,
  title,
  varient,
  colorName,
  price,
  stockOut,
  quantity,
  updateQuantity,
}) => {
  const quantities = [];

  for (let i = 1; i <= 99; i++) {
    quantities.push(
      <option key={i} defaultValue={i}>
        {i}
      </option>
    );
  }

  return (
    <article className="_2TDxO _3eNt3">
      <div className="_2GLQ8">
        <Link to={link}>
          <div
            className="_12cr2 _1Saxg _31IMi wBHJF"
            style={{
              backgroundImage: `url("${imageUrl}")`,
            }}
          >
            <div className="iUm2H" />
            <img
              className="_3YlSt"
              src={imageUrl}
              draggable="true"
              alt={title}
            />
          </div>
        </Link>
        <div className="_13SLo">
          <button className="_2I_KM">
            <svg
              width={5}
              height={21}
              viewBox="0 0 5 21"
              xmlns="http://www.w3.org/2000/svg"
            >
              <g fill="black" fillRule="evenodd">
                <circle cx="2.5" cy="2.5" r="2.5" />
                <circle cx="2.5" cy="10.5" r="2.5" />
                <circle cx="2.5" cy="18.5" r="2.5" />
              </g>
            </svg>
          </button>
          <div className="_3FOyR _2MbZC">
            <div className="_2olKO _2qvbE NCBqF">
              <h3 className="_1y-bK">
                <Link className="_3EfZE" to={link}>
                  {title}
                </Link>
              </h3>
              <div>
                <button type="button" className="_13v2H">
                  Edit
                </button>
                <button type="button" className="_13v2H _2WXX-">
                  Remove
                </button>
              </div>
            </div>
          </div>
          <div className="_1J2qp">
            <div className="_3FOyR _1UIvE">
              <div className="_2olKO _2qvbE NCBqF">
                <span>{varient}</span>
              </div>
            </div>
            <div className="_3FOyR _21yQ1">
              <div className="_2olKO _2qvbE NCBqF">
                <div>
                  <span className="_3ghie">
                    <span
                      className="vx9UB"
                      style={{ backgroundColor: "rgb(189, 182, 174)" }}
                    />
                    <span
                      className="vx9UB"
                      style={{ backgroundColor: "rgb(189, 182, 174)" }}
                    />
                  </span>
                  <span>{colorName}</span>
                </div>
              </div>
            </div>
          </div>
          <div className="_3FOyR _1ibq5">
            <div className="_2olKO _2qvbE NCBqF">
              <div className="_3iXMQ">
                <RemoveFromCartButton classes="_13v2H" item={{ id }} />
              </div>
            </div>
          </div>
          <div className="_3FOyR _19LPM">
            <div className="_2olKO _2qvbE NCBqF">
              <div className="_1UPbs">Qty {quantity}</div>
              <label className="_1LneS _2dmcv">
                <div className="_3QW13">
                  <select
                    className="_2lgH8"
                    defaultValue={quantity}
                    onChange={function (event) {
                      updateQuantity({ id, quantity: Number(event.target.value) });
                    }}
                  >
                    {quantities}
                  </select>
                  <svg
                    width={10}
                    height={4}
                    viewBox="0 0 10 4"
                    xmlns="http://www.w3.org/2000/svg"
                    className="_1raak"
                  >
                    <path
                      d="M1 0l4 4 4-4"
                      stroke="#676C77"
                      fill="none"
                      fillRule="evenodd"
                      strokeLinecap="square"
                    />
                  </svg>
                </div>
              </label>
            </div>
          </div>
          <div className="_3FOyR _2aKn1">
            <div className="_2olKO _2qvbE NCBqF">
              <span>${price*quantity}</span>
            </div>
          </div>
          {!stockOut && (
            <div className="_2U-Og">
              <p>Currently out of stock and will ship in 2-4 weeks.</p>
            </div>
          )}
        </div>
      </div>
      <div className="rAaj6" />
    </article>
  );
};

const mapDispatchToProps = (dispatch) => ({
  updateQuantity: (item) => dispatch(updateQuantity(item)),
});

export default connect(null, mapDispatchToProps)(CartItem);
