import React from "react";
import { connect } from "react-redux";

import DesignModal from "../desigin-modal/design-modal.component";
import MonogramModal from "../monogram-modal/monogram-modal.component";

const ModalSection = ({ section }) => {
  switch (section) {
    case "monogram":
      return <MonogramModal />;
    case "design":
      return <DesignModal />;
    default:
      return <MonogramModal />;
  }
};

const mapStateToProps = ({ modal: { section } }) => ({
  section
});

export default connect(mapStateToProps)(ModalSection);