import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { hideSidenav } from "../../redux/sidenav/sidenav.actions";

const ListItemOne = ({ title, link, hideSidenav }) => (
  <li className="tqFlA L9Jp_">
    <Link className="_3BaAZ" to={link} onClick={hideSidenav}>
      {title}
    </Link>
  </li>
);

const mapDispatchToProps = (dispatch) => ({
  hideSidenav: () => dispatch(hideSidenav())
})

export default connect(null, mapDispatchToProps)(ListItemOne);