import { typeSetDesignSection } from "./action.types";

const INITIAL_STATE = {
    section: "options"
  };
  
  const designModalReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
      case typeSetDesignSection:
          return {
              ...state,
              section: action.payload
          }
      default:
        return state;
    }
  };
  
  export default designModalReducer;