import React from "react";
import { withRouter } from "react-router-dom";

const ListItem = ({ link, title, history }) => (
  <li className="RjL3K">
    <button
        type="button"
        className="_10e4m"
        onClick={() => {
          history.push(link);
          window.scrollTo(0,0)
        }}
      >
        {title}
      </button>
  </li>
);

export default withRouter(ListItem);
