import { combineReducers } from "redux";
import persistReducer from "redux-persist/es/persistReducer";
import storage from "redux-persist/lib/storage";

import sidenavReducer from "./sidenav/sidenav.reducer";
import userReducer from "./user/user.reducer";
import modalReducer from "./modal/modal.reducer";
import monogramModalReducer from "./monogram-modal/monogram-modal.reducer";
import monogramReducer from "./monogram/monogram.reducer";
import cartReducer from "./cart/cart.reducer";
import cartDropdownReducer from "./cart-dropdown/cart-dropdown.reducer";
import designModalReducer from "./design-modal/design-modal.reducer";
import designReducer from "./design/design.reducer";

const persistConfig = {
  key: "root",
  storage,
  whitelist: ["user", "cart"],
};

const rootReducer = combineReducers({
  user: userReducer,
  sidenav: sidenavReducer,
  modal: modalReducer,
  monogramModal: monogramModalReducer,
  designModal: designModalReducer,
  monogram: monogramReducer,
  design: designReducer,
  cart: cartReducer,
  cartDropdown: cartDropdownReducer
});

export default persistReducer(persistConfig, rootReducer);