import React from "react";
import Arrangement from "./arrangement.component";

import {monogramInitials} from '../../../../../local-db/local-db';

const Arrangements = () => {
  return(
  <div className="if6Qw">
    <p className="_5E28o">Select Arrangement</p>
    {monogramInitials.map(({ id, title, letters }, index) => (
      <Arrangement key={index} id={id} title={title} letters={letters} />
    ))}
  </div>
)}

export default Arrangements;