import { typeHideSidenav, typeShowSidenav } from "./action.types";

const INITIAL_STATE = {
  hidden: true,
  section: "navigator"
};

const sidenavReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case typeShowSidenav:
      return {
        ...state,
        hidden: false,
        section: action.payload
      };
    case typeHideSidenav:
        return {
            ...state,
            hidden: true
        }
    default:
      return state;
  }
};

export default sidenavReducer;