import React from "react";
import { connect } from "react-redux";

import { showModal } from "../../redux/modal/modal.actions";

const ShowModalButton = ({ title, modalTitle, classes, showModal }) => (
  <span className={classes || ""} onClick={() => showModal(modalTitle)}>
    {title}
  </span>
);

const mapDispatchToProps = (dispatch) => ({
  showModal: (title) => dispatch(showModal(title))
});

export default connect(null, mapDispatchToProps)(ShowModalButton);