import React, { useState } from "react";

import Answer from './answer.component';

const Question = ({ title, answer, id }) => {
  
  const [down, setDown] = useState(false);

  const toggleQuestion = () => {
    setDown(!down)
  }
  
  return (
  <div id={id} className="_1q1cM" onClick={toggleQuestion}>
    <div className="_3Bsqr">
      <p className="IY1zP">{title}</p>
      <i className={`_208IU${down ? " _2CHhs" : ""}`}>
        <svg
          width={10}
          height={6}
          viewBox="0 0 10 6"
          xmlns="http://www.w3.org/2000/svg"
        >
          <g
            stroke="#676C77"
            fill="none"
            fillRule="evenodd"
            strokeLinecap="square"
          >
            <path d="M1 1l4.243 4.243" />
            <path d="M9.536 1L5.293 5.243" />
          </g>
        </svg>
      </i>
    </div>
    {
      down && <Answer description={answer} />
    }
  </div>
)}

export default Question;