import { createSelector } from "reselect";

const selectMonogram = (state) => state.monogram;

export const selectColor = createSelector(
  [selectMonogram],
  (monogram) => monogram.color
);
export const selectInitials = createSelector(
  [selectMonogram],
  (monogram) => monogram.initials
);
export const selectInitialsLetters = createSelector(
  [selectInitials],
  (initials) => initials.letters
);
export const selectTitle = createSelector(
  [selectMonogram],
  (monogram) => monogram.title
);
export const selectLimit = createSelector(
  [selectMonogram],
  (monogram) => monogram.limit
);
export const selectPaths = createSelector(
  [selectMonogram],
  (monogram) => monogram.paths
);