import React from "react";
import { connect } from "react-redux";
import InputField from "./input-field.component";
import { setPlacement } from "../../../../../redux/monogram/monogram.actions";

const Placement = ({ title, imageUrl, id, setPlacement, placement }) => (
  <div
    className={`_3LIWp${id === placement ? " FO3mY" : ""}`}
    onClick={() => {
        if (id !== placement) setPlacement(id)
    }}
  >
    <div
      className="_12cr2 _2aYev _31IMi wBHJF"
      style={{
        backgroundImage: `url(${imageUrl})`,
      }}
    >
      <div className="iUm2H" />
      <img className="_3YlSt" src={imageUrl} draggable="true" alt={title} />
    </div>
    <InputField title={title} id={id} />
  </div>
);

const mapStateToProps = ({ monogram: { placement } }) => ({
  placement,
});

const mapDispatchToProps = (dispatch) => ({
  setPlacement: (id) => dispatch(setPlacement(id))
});

export default connect(mapStateToProps, mapDispatchToProps)(Placement);