import React from "react";
import { Link } from "react-router-dom";

const ProductCardImage = ({ linkClass, link, classes, imageUrl }) => (
  <Link className={linkClass} to={link}>
    <div
      className={classes}
      style={{
        backgroundImage: `url("${imageUrl}")`,
      }}
    >
      <div className="iUm2H" />
      <img className="_3YlSt" src={imageUrl} draggable="true" alt={link} />
    </div>
  </Link>
);

export default ProductCardImage;