import React from "react";
import { Route, Switch } from "react-router-dom";

import "./style.css";

import { products } from "./local-db/local-db";

import ScrollToTop from "./components/scroll-to-top/scroll-to-top.component";

import BodyContainer from "./components/body-container/body-container";
import HomePage from "./pages/home/home";
import ContactUsPage from "./pages/contact-us/contact-us";
import AboutUsPage from "./pages/about-us/about-us";
import DoingBusinessWithUsPage from "./pages/doing-business-with-us/doing-business-with-us";
import PressPage from "./pages/press/press";
import GiftCardsPage from "./pages/gift-cards/gift-cards";
import NotFoundPage from "./pages/404-not-found/404-not-found";
import ProductsPage from "./pages/products/products";
import ProductPage from "./pages/product/product";
import InsidePage from "./pages/inside/inside";
import SignUpPage from "./pages/sign-up/sign-up";
import MyAccountPage from "./pages/my-account/my-account";
import FaqsPage from "./pages/faqs/faqs";
import CartPage from "./pages/cart/cart";
import SignInPage from "./pages/sign-in/sign-in";
import CheckoutPage from "./pages/checkout/checkout";
import MonogramPage from "./pages/monogram/monogram";

const App = () => {
  return (
    <BodyContainer>
      <ScrollToTop />
      <Switch>
        <Route exact path="/" component={HomePage} />
        <Route exact path="/cart" component={CartPage} />
        <Route exact path="/cart/checkout" component={CheckoutPage} />
        <Route exact path="/sign-in" component={SignInPage} />
        <Route exact path="/sign-up" component={SignUpPage} />
        <Route exact path="/contact-us" component={ContactUsPage} />
        <Route exact path="/about-us" component={AboutUsPage} />
        <Route
          exact
          path="/doing-business-with-us"
          component={DoingBusinessWithUsPage}
        />
        <Route exact path="/inside" component={InsidePage} />
        <Route exact path="/press" component={PressPage} />
        <Route exact path="/monogram" component={MonogramPage} />
        <Route exact path="/gift-cards" component={GiftCardsPage} />
        <Route exact path="/products" component={ProductsPage} />
        <Route exact path="/faqs" component={FaqsPage} re />
        <Route
          exact
          path="/my-account"
          render={() => <MyAccountPage section="profile" />}
        />
        <Route
          exact
          path="/my-account/orders"
          render={() => <MyAccountPage section="orders" />}
        />
        <Route
          exact
          path="/products/:id"
          render={({
            match: {
              params: { id },
            },
          }) => {
            const item = products.find((product) => product.id === Number(id));
            if (item) return <ProductPage {...item} />;

            return <NotFoundPage />
          }}
        />

        <Route path="/" component={NotFoundPage} />
      </Switch>
    </BodyContainer>
  );
};

export default App;