import React from "react";
import { connect } from "react-redux";
import { setPath } from "../../../../../redux/monogram/monogram.actions";

import MONOGRAMS from "../../../../../local-db/monograms";
import {
  selectLimit,
  selectTitle,
} from "../../../../../redux/monogram/monogram.selectors";

const Letter = ({ position, letter, limit, title, setPath }) => {
  const changeHandler = async (event) => {
    try {
      let letter = event.target.value[0];

      if (!letter) return;

      letter = letter.toUpperCase();
      // const response = await fetch(
      //   `/monograms/monogram-letter?title=${encodeURI(
      //     title
      //   )}&limit=${limit}&position=${position}&letter=${letter}`
      // ).then((res) => res.json());

      // const { error, path } = response;

      // if (error) return console.log(error);

      const path = MONOGRAMS.find(
        (monogram) =>
          monogram.limit === limit &&
          monogram.title === title &&
          monogram.letter === letter &&
          monogram.position === position
      ).path;

      setPath({
        position,
        letter,
        path,
      });
      // setLetter({
      //   letter,
      //   position
      // })
    } catch (error) {
      console.log(error);
    }
  };

  const inputHandler = (event) => {
    event.target.value = "";
  };

  return (
    <label className="_1dKTb field">
      <input
        type="text"
        pattern="[ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz]"
        className="m9jzC"
        required
        defaultValue={letter}
        maxLength={1}
        onClick={function (e) {
          e.target.select();
        }}
        onKeyDown={inputHandler}
        onChange={changeHandler}
      />
      <span className="_1hdKi">{position.toUpperCase()}</span>
    </label>
  );
};

const mapStateToProps = (state) => ({
  title: selectTitle(state),
  limit: selectLimit(state),
});

const mapDispatchToProps = (dispatch) => ({
  setPath: (obj) => dispatch(setPath(obj)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Letter);
