import React from "react";
import Card from "../components/card.component";
import Image from '../components/image.component';

const MainSection = ({ id, title, imageUrl, price, varient, link, colorName }) => (
  <section className="Jooqy" id="shop_the_look">
    <div className="_2ezKP wCFLC _3AP5M">
      <h3 className="_2D8fb">Shop the Look</h3>
    </div>
    <div className="_2ezKP h7hGr">
      <Image imageUrl="/assets/images/Bedding_Shop_Style_Alba2.jpg" title="Alba in Honey." />
      <div className="py6xf cFuRd">
        <div className="_3IvAE">
          <Card 
            id={id}
            title={title}
            imageUrl={imageUrl}
            varient={varient}
            price={price}
            link={link}
            colorName={colorName}
          />
        </div>
      </div>
    </div>
  </section>
);

export default MainSection;