import { addToCart, removeFromCart, updateItemQuantity } from './cart.utilities';

import {
  typeAddToCart,
  typeEmptyCart,
  typeRemoveFromCart,
  typeUpdateQuantity,
} from "./action.types";

const INITIAL_STATE = {
  items: [],
  totalItems: 0,
  totalPrice: 0
};

const cartReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case typeAddToCart:
      return addToCart(state, action.payload);
    case typeRemoveFromCart:
      return removeFromCart(state, action.payload);
    case typeEmptyCart:
      return {
        ...state,
        items: [],
        totalItems: 0,
        totalPrice: 0
      };
    case typeUpdateQuantity:
      return updateItemQuantity(state, action.payload);
    default:
      return state;
  }
};

export default cartReducer;