export const getChangedPaths = (paths, newPath) => ({
  ...paths,
  [newPath.position]: {
    letter: newPath.letter,
    path: newPath.path,
  },
});

export const getChangedLetters = (letters, newLetter) => {
  letters.find(item => item.position === newLetter.position).letter = newLetter.letter;

  return letters;
}