import { typeToggleCartDropdown } from "./action.types";

const INITIAL_STATE = {
    hidden: true
  };
  
  const cartDropdownReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
      case typeToggleCartDropdown:
        return {
          ...state,
          hidden: !state.hidden,
        };
      default:
        return state;
    }
  };
  
  export default cartDropdownReducer;