import React from "react";
import { connect } from "react-redux";

import {
  selectLimit,
  selectPaths,
} from "../../../../../redux/monogram/monogram.selectors";
import Letter from "./letter.component";

const Letters = ({ limit, paths }) => {
  return (
  <div className="hlA9g">
    <p className="_5E28o">Type below your word ({limit} character(s) max.)</p>
    <form className="_1rAT2">
      {
        (limit % 2 === 0 ? (
          ((<Letter letter={paths.left.letter} position="left" />),
          limit === 4
            ? ((
                <Letter
                  letter={paths.leftCenter.letter}
                  position="leftCenter"
                />
              ),
              (
                <Letter
                  letter={paths.rightCenter.letter}
                  position="rightCenter"
                />
              ))
            : "",
          (<Letter letter={paths.right.letter} position="right" />))
        ) : [limit > 1 ? <Letter key={0} letter={paths.left.letter} position="left" />: "",
        <Letter key={parseInt(limit/2)} letter={paths.center.letter} position="center" />,
        limit > 1 ? <Letter key={limit-1} letter={paths.right.letter} position="right" />: ""])
      }
    </form>
    <p className="oYVjP">
      For three letter monograms in this style, we recommend placing your last
      initial in the center position.{" "}
      <a
        rel="noopener noreferrer"
        target="_blank"
        href="/monogram#guide"
        className="_1Ppxy _2EXqQ"
      >
        Monogram Guide
      </a>
    </p>
  </div>
)};

const mapStateToProps = (state) => ({
  limit: selectLimit(state),
  paths: selectPaths(state),
});

export default connect(mapStateToProps)(Letters);
