import React from "react";
import { connect } from "react-redux";
import { setInitials } from "../../../../../redux/monogram/monogram.actions";
import { selectInitials } from "../../../../../redux/monogram/monogram.selectors";

const Arrangement = ({ id, title, letters, initials, setInitials }) => (
  <label
    className={`_3aoz5${id === initials.id ? " _18geF" : ""}`}
    onClick={() => setInitials({
      id, title, letters
    })}
  >
    <span className="_3xTzm">{title}</span>
    <input
      type="radio"
      className="_3CyZe"
      name="arrangement"
    />
  </label>
);

const mapStateToProps = (state) => ({
  initials: selectInitials(state)
});

const mapDispatchToProps = (dispatch) => ({
  setInitials: (initials) => dispatch(setInitials(initials)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Arrangement);
